"use client";

import { useState } from "react";
import "../../globals.css";
import { registerUser } from "../api/authAPI";

export default function RegisterPage() {

  const [form, setForm] = useState({
    firstName: "",
    lastName: "",
    email: "",
    emailOtp: "",
    mobile: "",
    mobileOtp: "",
    password: "",
    confirmPassword: "",
  });

  const [emailVerified, setEmailVerified] = useState(false);
  const [mobileVerified, setMobileVerified] = useState(false);

  function sendEmailOtp() {
    if (!form.email) return alert("Enter email first");
    alert(`OTP sent to ${form.email}`);
    setEmailVerified(true);
  }

  function sendMobileOtp() {
    if (!form.mobile) return alert("Enter mobile number first");
    alert(`OTP sent to ${form.mobile}`);
    setMobileVerified(true);
  }

  function handleSubmit(e) {
    e.preventDefault();

    if (form.password !== form.confirmPassword) {
      return alert("Passwords do not match");
    }

    alert("Registration successful!");
  }

  return (
    <div className="auth-container">
      <h2 className="auth-title">Create your account</h2>

      <form onSubmit={handleSubmit}>

        <input
          className="input-field"
          placeholder="First Name"
          value={form.firstName}
          onChange={e => setForm({ ...form, firstName: e.target.value })}
          required
        />

        <input
          className="input-field"
          placeholder="Last Name"
          value={form.lastName}
          onChange={e => setForm({ ...form, lastName: e.target.value })}
          required
        />

        {/* Email Section */}
        <div className="verify-row">
          <input
            className="input-field"
            placeholder="Email"
            value={form.email}
            onChange={e => setForm({ ...form, email: e.target.value })}
            required
          />
          <button type="button" onClick={sendEmailOtp} className="verify-btn">
            Verify
          </button>
        </div>

        {emailVerified && (
          <input
            className="input-field"
            placeholder="Enter Email OTP"
            value={form.emailOtp}
            onChange={e => setForm({ ...form, emailOtp: e.target.value })}
            required
          />
        )}

        {/* Mobile Section */}
        <div className="verify-row">
          <input
            className="input-field"
            placeholder="Mobile Number"
            value={form.mobile}
            onChange={e => setForm({ ...form, mobile: e.target.value })}
            required
          />
          <button type="button" onClick={sendMobileOtp} className="verify-btn">
            Verify
          </button>
        </div>

        {mobileVerified && (
          <input
            className="input-field"
            placeholder="Enter Mobile OTP"
            value={form.mobileOtp}
            onChange={e => setForm({ ...form, mobileOtp: e.target.value })}
            required
          />
        )}

        {/* Password Section */}
        <input
          type="password"
          className="input-field"
          placeholder="Password"
          value={form.password}
          onChange={e => setForm({ ...form, password: e.target.value })}
          required
        />

        <input
          type="password"
          className="input-field"
          placeholder="Confirm Password"
          value={form.confirmPassword}
          onChange={e => setForm({ ...form, confirmPassword: e.target.value })}
          required
        />

        <button className="auth-button">Register</button>
      </form>

      <p className="auth-link">
        Already have an account? <a href="/auth/login">Login</a>
      </p>
    </div>
  );
}


async function handleRegister() {
  const response = await registerUser({
    email,
    password
  });

  console.log(response);

  if (response.message) {
    alert("Registered successfully");
  } else {
    alert("Registration failed");
  }
}
